<template>
	<div>
		<!--  -->
		<el-breadcrumb separator="/" class="admin-nav">
		  <el-breadcrumb-item :to="{ path: '/adminHome' }"></el-breadcrumb-item>
		  <el-breadcrumb-item></el-breadcrumb-item>
		  <el-breadcrumb-item></el-breadcrumb-item>
		</el-breadcrumb>
		
		<!--  -->
		<el-card class="category-list-card">
			<div slot="header">
			    <el-button  type="primary" @click="addDialogVisible=true"></el-button>
			</div>
			<div>
				<!--  -->
				<el-table :data="categoryList" style="width: 100%">
					<el-table-column prop="categoryId" label="ID"/>
					<el-table-column prop="categoryImage" label="">
						<template slot-scope="scope">
						<img :src="scope.row.categoryImage" width="50" height="50"/>
						</template>
					</el-table-column>
					<el-table-column prop="categoryName" label=""/>
					<el-table-column label="" prop="isRecommend" >
						<template slot-scope="scope">
								<el-tag dark type="success" v-if="scope.row.isRecommend == 0"></el-tag>
							<el-tag dark type="warning" v-if="scope.row.isRecommend == 1"></el-tag>
						</template>
					</el-table-column>
					<el-table-column label="">
						<template slot-scope="scope">
							<el-tooltip effect="dark" content="" placement="top" :enterable="false">
								<el-button type="primary" size="mini" icon="el-icon-edit" @click="showUpdateDialog(scope.row)"></el-button>
							</el-tooltip>
							<el-tooltip effect="dark" content="" placement="top" :enterable="false">
								<el-button type="danger" size="mini" icon="el-icon-delete" @click="handleDeleteCategory(scope.row.categoryId)"></el-button>
							</el-tooltip>
							<el-tooltip v-if="!scope.row.isRecommend" effect="dark" content="" placement="top" :enterable="false">
								<el-button type="primary" size="mini" icon="el-icon-plus" @click="handleRecommend(scope.row.categoryId,1)"></el-button>
							</el-tooltip>
							<el-tooltip v-else effect="dark" content="" placement="top" :enterable="false">
								<el-button size="mini" icon="el-icon-close" @click="handleRecommend(scope.row.categoryId,0)"></el-button>
							</el-tooltip>
						</template>
					</el-table-column>
				</el-table>
			</div>
		</el-card>
		
		<!--  -->
		<el-dialog title="" :visible.sync="addDialogVisible" width="60%" @close="addDialogClosed">
			<!--  -->
			<el-form ref="addFormRef" :model="addForm" :rules="addFormRules" label-width="80px">
					<el-form-item label="" prop="categoryName">
						<el-input v-model="addForm.categoryName"></el-input>
					</el-form-item>
					<el-form-item>
						<el-upload action="*" ref="upload" list-type="picture-card"
							:auto-upload="false"
							:http-request="handleAddCategory"
							:limit="1">
							<img v-if="imageUrl" :src="imageUrl">
							<i v-else class="el-icon-plus avatar-uploader-icon"></i>
						</el-upload>
					</el-form-item>
				</el-form>
			<!--  -->
			<span slot="footer">
				<el-button @click="addDialogVisible=false"> </el-button>
				<el-button type="primary" @click="upload"> </el-button>
			</span>
		</el-dialog>
		
		<!--  -->
		<el-dialog title="" :visible.sync="updateDialogVisible" width="60%" @close="updateDialogClosed">
			<!--  -->
			<el-form ref="updateFormRef" :model="updateForm" :rules="updateFormRules" label-width="80px">
					<el-form-item label="ID" prop="categoryId">
						<el-input v-model="updateForm.categoryId" disabled></el-input>
					</el-form-item>
					<el-form-item label="" prop="categoryImage">
						<img :src="updateForm.categoryImage" width="150"></img>
						<el-upload action list-type="picture" :http-request="handleUploadImage">
							<el-button size="small" type="primary"></el-button>
						</el-upload>
					</el-form-item>
					<el-form-item label="" prop="categoryName">
						<el-input v-model="updateForm.categoryName"></el-input>
						<el-button type="primary" @click="handleUpdateCategoryName"></el-button>
					</el-form-item>
				</el-form>
			<!--  -->
			<span slot="footer">
				<el-button @click="updateDialogVisible=false"> </el-button>
			</span>
		</el-dialog>
	</div>
</template>

<script>
	import { addCategory,getCategory,deleteCategory,recommendCategory,uploadCategoryImg,updateCategoryName } from '@/api/admin'
	import { responseStatus } from '@/utils/responseStatusUtil'
	export default {
		name: 'categoryList',
		inject: ['reload'],
		data(){
			return {
				imageUrl: '',
				//
				queryInfo:{
					//
					pageTotal: 0
				},
				//&
				addDialogVisible: false,
				//&
				updateDialogVisible: false,
				//
				addForm: {
					categoryName: ''
				},
				//
				updateForm: {
					categoryId: '',
					categoryImage: '',
					categoryName: ''
				},
				//
				addFormRules: {
					categoryName: [
						{ required:true, message:'', trigger:'blur'}
					]
				},
				//
				updateFormRules: {
					categoryId: [
						{ required:true, message:'ID', trigger:'blur'}
					],
					categoryName: [
						{ required:true, message:'', trigger:'blur'}
					]
				},
				//
				categoryList: [],
			}
		},
		methods:{
			
			/*  */
			handleGetCategory(){
				getCategory().then(res => {
					if(responseStatus(res)){
						this.categoryList = res.data.data;
					}
				})
			},
			
			//
			handleUploadImage(file){
				const formData = new FormData();
					formData.append('categoryId',this.updateForm.categoryId);
					formData.append('categoryImage', file.file);
					uploadCategoryImg(formData).then(res => {
						if(responseStatus(res)){
							this.$message.success("!!!");
							this.updateDialogVisible = false;
							this.handleGetCategory();
							this.reload();
						}
					})
			},
			
			
			handleUpdateCategoryName(){
				const data = {
					categoryId: this.updateForm.categoryId,
					categoryName: this.updateForm.categoryName
				}
				updateCategoryName(data).then(res => {
					if(responseStatus(res)){
						this.$message.success("");
						this.updateDialogVisible = false;
						this.handleGetCategory();
						this.reload();
					}
				})
			},
			
			//
			addDialogClosed(){
				this.$refs.addFormRef.resetFields(); 
			},
			//
			showUpdateDialog(category){
				this.updateForm.categoryId = category.categoryId,
				this.updateForm.categoryImage = category.categoryImage,
				this.updateForm.categoryName = category.categoryName,
				this.updateDialogVisible = true;
				
			},
			//
			updateDialogClosed(){
				this.$refs.updateFormRef.resetFields(); //
			},
			/*  */
			handleAddCategory(file){
				this.$refs.addFormRef.validate(valid => {
					const categoryFormData = new FormData();
					categoryFormData.append('categoryName',this.addForm.categoryName);
					categoryFormData.append('file', file.file);
					if(valid){
						addCategory(categoryFormData).then(res => {
							if(responseStatus(res)){
								this.$message.success("!!!");
								this.addDialogVisible = false;
								this.handleGetCategory();
							}
						})
					}
				})
			},
			upload(){
				this.$refs.upload.submit();
			},
			
			/*  */
			handleDeleteCategory(categoryId){
				const data = {
					categoryId: categoryId
				}
				deleteCategory(data).then(res => {
					if(responseStatus(res)){
						this.$message.success("");
						this.handleGetCategory();
					}
				})
				
			},
			//
			updateCategory(){
				this.$refs.updateFormRef.validate(valid =>{
					if(valid){
						this.$axios.post('admin/updateCategory',this.$qs.stringify({ 
							categoryId: this.updateForm.categoryId,
							categoryImage: this.updateForm.categoryImage,
							categoryName: this.updateForm.categoryName}))
								   .then(res => {
									   //console.log(res);
									   if(res.status !== 200)
											this.$message.error("!!!");
										else if(res.data.status !== 10000)
											this.$message.error(res.data.msg+"!!!");
										else
											this.$message.success("!!!");
								   })
					}else return;
				})
			},
			
			/*  */
			handleRecommend(categoryId,type){
				const data = {
					categoryId: categoryId,
					type: type
				}
				recommendCategory(data).then(res => {
					if(responseStatus(res)){
						this.$message.success("");
						this.updateDialogVisible = false;
					}
				})
			}
			
		},
		created() {
			this.handleGetCategory();
		}
	}
</script>

<style scoped="scoped">
	.admin-nav {
		margin: 0 20px;
	}
	.category-list-card {
		margin: 20px;
		padding-top: 20px;
		border-radius: 5px;
	}
	.pagination {
		margin-top: 20px;
		text-align: right;
	}
</style>
