<template>
	<!--  -->
	<div class="userInfo-container">
		<!--  -->
		<div class="userInfo-header">
			<!--  -->
			<div class="avatar">
				<el-avatar :size="80" :src="user.headSculpture" @click.native="isHost?updateAvatarDialogVisible=true : updateAvatarDialogVisible=false"></el-avatar>
			</div>
			<div class="user-info-msg">
				<span>{{user.username}}</span>
				<div>
					<span v-if="user.sex == 0"><i class="iconfont icon-suo color-suo" style="font-size: 20px"></i></span>
					<span v-if="user.sex == 1"><i class="iconfont icon-nan color-nan" style="font-size: 20px"></i></span>
					<span v-if="user.sex == 2"><i class="iconfont icon-nv color-nv" style="font-size: 20px"></i></span>
				</div>
				<span>
					: {{user.personalizedSignature}}
					<i v-if="isHost" class="el-icon-edit" @click="updateDialogVisible=true" style="cursor: pointer;"></i>
				</span>
				<span v-if="isHost">
					<el-button type="text" class="hidden-sm-and-up" style="padding: 0;" @click="updateIntervalDiaglogVisible = true"></el-button>
				</span>
			</div>
			<div class="user-info-btn hidden-xs-only" v-if="isHost">
				<el-button size="small" @click="updateIntervalDiaglogVisible = true"></el-button>
			</div>
		</div>
		<!--  -->
		<div class="user-info-detail">
			<p class="title"></p>
			<div class="user-info-detail-box">
				<div>
					<span><i class="el-icon-map-location icon-margin"></i>{{user.school != null?user.school:""}}</span>
					<span><i class="el-icon-chat-round icon-margin"></i>QQ{{user.qq != null?user.qq:""}}</span>
					<span><i class="el-icon-eleme icon-margin"></i>{{user.personalWebsite != null?user.personalWebsite:""}}</span>
					<span><i class="el-icon-chat-dot-round icon-margin"></i>{{user.wechat != null?user.wechat:""}}</span>
					<!-- xs -->
					<div class="hidden-sm-and-up">
						<span><i class="el-icon-message icon-margin"></i>{{user.email != null?user.email:""}}</span>
						<span><i class="el-icon-edit icon-margin"></i>{{user.wordNumber != null?user.wordNumber:""}}</span>
						<span><i class="el-icon-edit icon-margin"></i>{{fansNum}}</span>
						<span><i class="el-icon-star-off icon-margin"></i>{{followNum}}</span>
					</div>
				</div>
				<div class="hidden-xs-only">
					<span><i class="el-icon-message icon-margin"></i>{{user.email != null?user.email:""}}</span>
					<span><i class="el-icon-edit icon-margin"></i>{{user.wordNumber != null?user.wordNumber:""}}</span>
					<span><i class="el-icon-edit icon-margin"></i>{{fansNum}}</span>
					<span><i class="el-icon-star-off icon-margin"></i>{{followNum}}</span>
				</div>
			</div>
		</div>
		
		<!--  -->
		<el-dialog title="" :visible.sync="updateDialogVisible" width="40%" @close="updateDialogClosed">
			<el-form ref="updateFormRef" :model="updateForm" :rules="updateFormRules" label-width="80px">
					<el-form-item label="" prop="sign">
						<el-input v-model="updateForm.sign" maxlength="100"></el-input>
					</el-form-item>
				</el-form>
			<span slot="footer">
				<el-button @click="updateDialogVisible=false"> </el-button>
				<el-button type="primary" @click="updateSign"> </el-button>
			</span>
		</el-dialog>
		
		
		<!--  -->
		<el-dialog title="" :visible.sync="updateIntervalDiaglogVisible" width="60%" @close="updateIntervalDialogClosed()">
			<el-form ref="updateIntervalFormRef" :model="updateIntervalForm" :rules="updateIntervalFormRules" label-width="80px">
					<el-form-item label=": " prop="personalWebsite">
						<el-input v-model="updateIntervalForm.personalWebsite" maxlength="100"  placeholder=""></el-input>
					</el-form-item>
					<el-form-item label="QQ: " prop="qq">
						<el-input v-model="updateIntervalForm.qq" maxlength="20" placeholder="QQ"></el-input>
					</el-form-item>
					<el-form-item label=": " prop="wechat">
						<el-input v-model="updateIntervalForm.wechat" maxlength="20" placeholder=""></el-input>
					</el-form-item>
					<el-form-item label=": " prop="sex">
						<el-select v-model="updateIntervalForm.sex" placeholder="">
							<el-option label="" value="0"></el-option>
							<el-option label="" value="1"></el-option>
							<el-option label="" value="2"></el-option>
						</el-select>
					</el-form-item>
					<el-form-item label=": " prop="school">
						<el-input v-model="updateIntervalForm.school" maxlength="100" placeholder=""></el-input>
					</el-form-item>
				</el-form>
			<span slot="footer">
				<el-button @click="updateIntervalDiaglogVisible=false"> </el-button>
				<el-button type="primary" @click="handleEditInterval"> </el-button>
			</span>
		</el-dialog>
		
		<!--  -->
		<el-dialog title="" :visible.sync="updateAvatarDialogVisible" width="60%">
			<el-upload action="*" ref="upload" list-type="picture-card"
				:auto-upload="false"
				:http-request="handleUpdateAvatar"
				:limit="1">
				<img v-if="imageUrl" :src="imageUrl">
				<i v-else class="el-icon-plus avatar-uploader-icon"></i>
			</el-upload>
			<span slot="footer">
				<el-button @click="updateAvatarDialogVisible=false"> </el-button>
				<el-button type="primary" @click="upload"> </el-button>
			</span>
		</el-dialog>
	</div>
</template>

<script>
	import { getUser, getFollowNum, getFansNum, updateInterval,updateAvatar } from '@/api/user'
	import { responseStatus } from '@/utils/responseStatusUtil'
	export default {
		props: {
			userId: { type: Number, requires: false },
			isHost: { type: Boolean, requires: false }
		},
		data(){
			return {
				user: {},
				//
				imageUrl: '',
				updateIntervalForm: {
					personalWebsite: null,
					qq: null,
					wechat: null,
					sex: null,
					school: null
				},
				//
				updateForm: {
					sign: ''
				},
				fansNum: 0,
				followNum: 0,
				//&
				updateDialogVisible: false,
				//&
				updateIntervalDiaglogVisible: false,
				//&
				updateAvatarDialogVisible: false,
				
				//
				updateIntervalFormRules: {
					sex: [
						{ required:true, message:'', trigger:'blur'}
					]
				},
				//
				updateFormRules: {
					sign: [
						{ required:true, message:'', trigger:'blur'}
					],
				},
			}
		},
		methods: {
			/*  */
			handleGetUser(userId){
				if(this.userId == null && this.isHost == false)
					return 
				const data = {
					userId: userId
				}
				getUser(data).then(res => {
					if(responseStatus(res)){
						this.user = res.data.data;
					}
				})
			},
			
			/*  */
			handleGetFollowNum(userId){
				const data = {
					userId: userId
				}
				getFollowNum(data).then(res => {
					if(responseStatus(res))
						this.followNum = res.data.data; 
				})  	
			},
			
			/*  */
			handleGetFansNum(userId){
				const data = {
					userId: userId
				}
				getFansNum(data).then(res => {
					if(responseStatus(res))
						this.fansNum = res.data.data; 
				})
			},
			
			/*  */
			updateSign(){
				this.$axios.post('/user/update',this.$qs.stringify({
					personalizedSignature: this.updateForm.sign }))
						   .then(res => {
								if(res.status !== 200)
								   	return this.$message.error("!!!");
								else if(res.data.status !== 10000)
								   	this.$message.error(res.data.msg+"!!!");
								else{
									this.updateDialogVisible = false;
									this.$message.success("!!!");
									this.handleGetUser(this.userId);
								}		   	
							})
			},
			
			/*  */
			handleEditInterval() {
				this.$refs.updateIntervalFormRef.validate(valid => {
					if(valid){
						updateInterval(this.updateIntervalForm).then(res => {
							if(responseStatus(res)){
								this.$message.success("");
								this.updateIntervalDiaglogVisible = false;
								this.handleGetUser(this.userId);
							}
						})
					}	
				})	
			},
			
			//
			handleUpdateAvatar(file){
				const formData = new FormData();
				formData.append('file', file.file);
				updateAvatar(formData).then(res => {
					if(responseStatus(res)){
				    	this.updateAvatarDialogVisible = false;
				    	this.$message.success("!!!");
						this.handleGetUser();
						this.$bus.$emit('change');
					}		   	
				})
			},
			upload(){
				this.$refs.upload.submit();
			},
			
			/*  */
			//
			updateIntervalDialogClosed(formRef){
				this.$refs.updateIntervalFormRef.resetFields(); //
			},
			//
			updateDialogClosed(){
				this.$refs.updateFormRef.resetFields(); //
			},
		},
		mounted() {
			this.handleGetUser(this.userId);
			this.handleGetFansNum(this.userId);
			this.handleGetFollowNum(this.userId);
		}
	}
</script>

<style lang="less" scoped>
	/*  */
	.userInfo-container{
		min-height: 240px;
		font-size: 14px;
		padding: 20px 40px;
		border-radius: 10px;
		background: url(~@/assets/background/background.jpg) no-repeat;
		background-size: 100% 100%;
		//
		.userInfo-header{
			margin-bottom: 20px;
			.avatar {
				display: inline-block;
			}
			//
			.user-info-msg {
				display: inline-block;
				margin-left: 16px;
				span {
					display: block;
					margin-top: 5px;
				}
				>span:nth-child(1) {
					font-size: 18px;
					font-weight: 700;
					margin-top: 0px;
				}
			}
			.user-info-btn {
				float: right;
			}
		}
		//
		.user-info-detail {
			margin-left: 16px;
			p {
				margin: 10px 0;
			}
			.user-info-detail-box {
				display: flex;
				justify-content: space-between;
				max-width: 800px;
			}
			span {
				display: block;
				margin-bottom: 10px;
			}
		}
	}
</style>
