import axios from 'axios'
import qs from 'qs'

import service from '@/network/request'

/**
 * 
 */

//
export const setGoodPost = (data)=>{
	return axios.put('/goodPost', qs.stringify(data));
}

//
export const cancelGoodPost = (data)=>{
	return axios.delete('/goodPost', {params: data});
}

//
export const checkGoodPost = (data)=>{
	return axios.get('/checkGood', {params: data});
}


/**
 * 
 */
export const getRandomPosts = () => {
	return service.get('/getRandomPosts');
}