<template>
	<el-container class="admin-home-container">
		<!--  -->
		<el-header class="color-back">
			<h3></h3>
			<!--  -->
			<el-dropdown @command="handleCommand">
				<el-avatar :size="40" :src="user.headSculpture"></el-avatar>
				<el-dropdown-menu slot="dropdown">
					<el-dropdown-item icon="el-icon-circle-close" command="/home"></el-dropdown-item>
					<el-dropdown-item icon="el-icon-circle-close" command="/logout" @click.native="handleLogout"></el-dropdown-item>
				</el-dropdown-menu>
			</el-dropdown>
		</el-header>
		
		<!--  -->
		<el-container>
			<!--  -->
			<el-aside :width="isCollapse? '64px' : '200px'" class="color-back">
				<!--  -->
				<el-menu background-color="#282c34" text-color="#FFFFFF" active-text-color="#409EFF" 
						 :collapse="isCollapse" :collapse-transition="false" router
						 :default-active="$route.path">
					<!--  -->
					<el-menu-item class="toggle-button" @click="toggleCollapse">
						<i class="el-icon-s-fold"></i>
						<span slot="title"></span>
					</el-menu-item>
					<el-submenu index="1">
					    <template slot="title">
					        <i class="el-icon-user"></i>
					        <span></span>
					    </template>
					    <el-menu-item-group>
					        <el-menu-item :index="index[0]">
								<i class="el-icon-tickets"></i>
								<span></span>
							</el-menu-item>
							<el-menu-item :index="index[1]">
								<i class="el-icon-lock"></i>
								<span></span>
							</el-menu-item>
					    </el-menu-item-group>
					</el-submenu>
					<el-submenu index="2">
					    <template slot="title">
					        <i class="el-icon-document"></i>
					        <span></span>
					    </template>
					    <el-menu-item-group>
					        <el-menu-item :index="index[2]">
								<i class="el-icon-tickets"></i>
								<span></span>
							</el-menu-item>
							<el-menu-item :index="index[3]">
								<i class="el-icon-delete"></i>
								<span></span>
							</el-menu-item>
					    </el-menu-item-group>
					</el-submenu>
					<el-submenu index="3">
					    <template slot="title">
					        <i class="el-icon-menu"></i>
					        <span></span>
					    </template>
					    <el-menu-item-group>
					        <el-menu-item :index="index[4]">
								<i class="el-icon-tickets"></i>
								<span></span>
							</el-menu-item>
					    </el-menu-item-group>
					</el-submenu>
					<el-submenu index="4">
					    <template slot="title">
					        <i class="el-icon-menu"></i>
					        <span></span>
					    </template>
					    <el-menu-item-group>
					        <el-menu-item :index="index[5]">
								<i class="el-icon-tickets"></i>
								<span></span>
							</el-menu-item>
							<el-menu-item :index="index[6]">
								<i class="el-icon-tickets"></i>
								<span></span>
							</el-menu-item>
					    </el-menu-item-group>
					</el-submenu>
				</el-menu>
			</el-aside>
			<!--  -->
			<el-main>
				<router-view></router-view>
			</el-main>
		</el-container>
	</el-container>
</template>

<script>
	import { logout } from '@/api/login'
	import { responseStatus } from '@/utils/responseStatusUtil'
	export default {
		name: 'adminHome',
		data(){
			return {
				user: {},
				//
				isCollapse: true,
				//
				activePath: '/adminUserList',
				//
				index: [
					'/adminUserList',
					'/banUser',
					'/adminPostList',
					'/trashStation',
					'/categoryList',
					'/adminBannerList',
					'/addBanner',
				]
			}
		},
		created() {
			//this.activePath = window.sessionStorage.getItem('activePath'),
			this.user = JSON.parse(window.sessionStorage.getItem('user'));
		},
		methods: {
			//
			toggleCollapse(){
				this.isCollapse = !this.isCollapse;
			},
			//
			/*saveNavState(activePath){
				window.sessionStorage.setItem('activePath',activePath);
				this.activePath = activePath;
			},*/
			
			/*  */
			handleCommand(command){
				this.$router.push(command);
			},
			
			/*  */
			handleLogout(){
				logout().then(res =>{
					if(responseStatus(res)){
						window.sessionStorage.clear();
						window.localStorage.clear();
						this.$router.push('login');
					}
				});	
			}
		}
	}
</script>

<style lang="less" scoped="scoped">
	.admin-home-container {
		height: 100%;
		background-color: #EAEDF1;
		/*  */
		.el-header {
			display: flex;
			align-items: center;
			justify-content: space-between;
			//
			h3 {
				color: #FFFFFF;
				font-size: 25px;
				font-weight: bold;
			}
		}
	}
</style>
